(0, 55, 132); // #003784\n\n// red\n$lightRed: rgb(255, 247, 247); // #fff7f7\n$coral: rgb(255, 186, 186); // #ffbaba\n$indianRed: rgb(255, 82, 82); // #ff5252\n$crimson: rgb(217, 34, 35); // #d92223\n$darkRed: rgb(155, 0, 0); // #9b0000\n\n// green\n$lightGreen: rgb(230, 252, 246); // #e6fcf6\n$paleGreen: rgb(135, 233, 200); // #87e9c8\n$springGreen: rgb(54, 205, 153); // #36cd99\n$seaGreen: rgb(12, 167, 111); // #0ca76f\n$darkGreen: rgb(0, 87, 58); // #00573a\n\n// orange\n$lightOrange: rgb(255, 252, 243); // #fffcf3\n$khaki: rgb(255, 234, 192); // #ffeac0\n$gold: rgb(255, 195, 90); // #ffc35a\n$orange: rgb(255, 163, 12); // #ffa30c\n$darkOrange: rgb(238, 104, 0); // #ee6800\n\n// gray\n$lightGray: rgb(246, 247, 248); // #f6f7f8\n$clouds: rgb(239, 240, 243); // #eff0f3\n$concrete: rgb(194, 199, 209); // #c2c7d1\n$asbestos: rgb(157, 164, 178); // #9da4b2\n$darkGray: rgb(126, 137, 156); // #7e899c\n\n/* stylelint-disable property-case */\n:export {\n  gainsBoro: $gainsBoro;\n  airForce: $airForce;\n  steel: $steel;\n  space: $space;\n  navy: $navy;\n  lightBlue: $lightBlue;\n  skyBlue: $skyBlue;\n  deepSkyBlue: $deepSkyBlue;\n  dodgeBlue: $dodgeBlue;\n  darkBlue: $darkBlue;\n  babyBlue: $babyBlue;\n  powderBlue: $powderBlue;\n  royalBlue: $royalBlue;\n  blue: $blue;\n  midNightBlue: $midNightBlue;\n  lightRed: $lightRed;\n  coral: $coral;\n  indianRed: $indianRed;\n  crimson: $crimson;\n  darkRed: $darkRed;\n  lightGreen: $lightGreen;\n  paleGreen: $paleGreen;\n  springGreen: $springGreen;\n  seaGreen: $seaGreen;\n  darkGreen: $darkGreen;\n  lightOrange: $lightOrange;\n  khaki: $khaki;\n  gold: $gold;\n  orange: $orange;\n  darkOrange: $darkOrange;\n  lightGray: $lightGray;\n  clouds: $clouds;\n  concrete: $concrete;\n  asbestos: $asbestos;\n  darkGray: $darkGray;\n  theme: $theme;\n  white: $white;\n  black: $black;\n}\n/* stylelint-enable property-case */\n","\n@mixin animate($animation, $duration: 0.3s, $delay: 0s) {\n  animation-name: $animation;\n  animation-duration: $duration;\n  animation-delay: $delay;\n  animation-fill-mode: both;\n}\n\n@mixin transition($rules: all, $delay: 0.3s, $type: ease) {\n  transition: $rules $delay $type;\n}\n\n@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n\n@keyframes fadeInLeft {\n  from {\n    opacity: 0;\n    visibility: hidden;\n    transform: translate(100px);\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n    transform: translate(0);\n  }\n}\n\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n    visibility: visible;\n  }\n\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n\n@keyframes fadeOutIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  90% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n","@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n@import '@upp/chrome/styles/mixins';\n@import '@upp/chrome/styles/media';\n\n.input {\n  height: 40px;\n  padding: 8px 12px;\n  padding-right: 30px;\n  border: 1px solid $concrete;\n  border-radius: 8px;\n  box-sizing: border-box;\n  -webkit-appearance: textfield;\n  -webkit-appearance: none;\n\n  @include transition();\n\n  &[type=search]::-webkit-search-decoration,\n  &[type=search]::-webkit-search-cancel-button,\n  &[type=search]::-webkit-search-results-button,\n  &[type=search]::-webkit-search-results-decoration {\n    display: none;\n  }\n\n  &[type=number] {\n    -moz-appearance: textfield;\n  }\n\n  &:focus {\n    border-color: $deepSkyBlue;\n  }\n\n  &:read-only,\n  &:disabled {\n    color: $asbestos;\n    cursor: not-allowed;\n    border-color: $clouds;\n    background: $clouds;\n    box-shadow: none;\n\n    &:hover {\n      border-color: $clouds !important;\n    }\n  }\n\n  &::placeholder {\n    color: $concrete;\n  }\n\n  &.error {\n    border-color: $crimson;\n\n    --box-shadow-color: #{$crimson};\n  }\n}\n\n.container {\n  position: relative;\n  width: 100%;\n  min-width: 200px;\n  max-width: 490px;\n\n  @media #{$phone} {\n    min-width: 100%;\n  }\n\n  &:hover {\n    .input {\n      border-color: $deepSkyBlue;\n    }\n  }\n}\n\n.inputWrapper {\n  position: relative;\n  margin-top: 5px;\n\n  .inputWithIcon {\n    padding-right: 55px;\n  }\n\n  .icon {\n    position: absolute;\n    top: 50%;\n    right: 12px;\n    width: 16px;\n    height: 16px;\n    transform: translateY(-50%);\n\n    path {\n      fill: $asbestos;\n    }\n  }\n\n  .iconCross {\n    width: 12px;\n    height: 12px;\n    opacity: 0;\n    cursor: pointer;\n\n    path {\n      fill: $asbestos;\n\n      @include transition(fill);\n    }\n\n    &:hover {\n      path {\n        fill: $concrete;\n      }\n    }\n\n    &.iconCrossRight {\n      right: 35px;\n    }\n  }\n}\n\n.show {\n  @include animate(fadeIn);\n}\n\n.hidden {\n  @include animate(fadeOut);\n}\n"],"sourceRoot":""}]);
