); // #ee6800\n\n// gray\n$lightGray: rgb(246, 247, 248); // #f6f7f8\n$clouds: rgb(239, 240, 243); // #eff0f3\n$concrete: rgb(194, 199, 209); // #c2c7d1\n$asbestos: rgb(157, 164, 178); // #9da4b2\n$darkGray: rgb(126, 137, 156); // #7e899c\n\n/* stylelint-disable property-case */\n:export {\n  gainsBoro: $gainsBoro;\n  airForce: $airForce;\n  steel: $steel;\n  space: $space;\n  navy: $navy;\n  lightBlue: $lightBlue;\n  skyBlue: $skyBlue;\n  deepSkyBlue: $deepSkyBlue;\n  dodgeBlue: $dodgeBlue;\n  darkBlue: $darkBlue;\n  babyBlue: $babyBlue;\n  powderBlue: $powderBlue;\n  royalBlue: $royalBlue;\n  blue: $blue;\n  midNightBlue: $midNightBlue;\n  lightRed: $lightRed;\n  coral: $coral;\n  indianRed: $indianRed;\n  crimson: $crimson;\n  darkRed: $darkRed;\n  lightGreen: $lightGreen;\n  paleGreen: $paleGreen;\n  springGreen: $springGreen;\n  seaGreen: $seaGreen;\n  darkGreen: $darkGreen;\n  lightOrange: $lightOrange;\n  khaki: $khaki;\n  gold: $gold;\n  orange: $orange;\n  darkOrange: $darkOrange;\n  lightGray: $lightGray;\n  clouds: $clouds;\n  concrete: $concrete;\n  asbestos: $asbestos;\n  darkGray: $darkGray;\n  theme: $theme;\n  white: $white;\n  black: $black;\n}\n/* stylelint-enable property-case */\n","\n@mixin animate($animation, $duration: 0.3s, $delay: 0s) {\n  animation-name: $animation;\n  animation-duration: $duration;\n  animation-delay: $delay;\n  animation-fill-mode: both;\n}\n\n@mixin transition($rules: all, $delay: 0.3s, $type: ease) {\n  transition: $rules $delay $type;\n}\n\n@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n\n@keyframes fadeInLeft {\n  from {\n    opacity: 0;\n    visibility: hidden;\n    transform: translate(100px);\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n    transform: translate(0);\n  }\n}\n\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n    visibility: visible;\n  }\n\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n\n@keyframes fadeOutIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  90% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n","@import '@upp/chrome/styles/media';\n@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n\n.button {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  height: 40px;\n  padding: 8px 12px;\n  color: $navy;\n  font-size: 14px;\n  cursor: pointer;\n  border-width: 1px;\n  border-radius: 8px;\n  border-style: solid;\n\n  @include transition();\n\n  .icon {\n    flex: none;\n    margin-left: 12px;\n    width: 16px;\n    height: 16px;\n  }\n\n  &:disabled {\n    cursor: not-allowed;\n    border-color: $asbestos;\n\n    .icon {\n      path {\n        fill: $asbestos;\n      }\n    }\n  }\n\n  @media #{$phone} {\n    width: 100%;\n  }\n\n  &.secondary {\n    background: none;\n  }\n}\n\n.input {\n  position: absolute;\n  top: -1px;\n  right: -1px;\n  bottom: -1px;\n  left: -1px;\n  width: calc(100% + 2px);\n  height: calc(100% + 2px);\n  opacity: 0;\n  cursor: pointer;\n}\n\n.title {\n  text-overflow: ellipsis;\n  overflow: hidden !important;\n  white-space: nowrap;\n}\n\n.default {\n  border-color: $clouds;\n  background: $lightGray;\n\n  &:hover {\n    background: $clouds;\n  }\n\n  &:disabled {\n    background: $concrete;\n  }\n\n  &.secondary {\n    &:hover {\n      background: $lightGray;\n    }\n\n    &:disabled {\n      background: $clouds;\n    }\n  }\n}\n\n.primary {\n  color: #fff;\n  border-color: $darkBlue;\n  background: $darkBlue;\n\n  &:hover {\n    background: $navy;\n  }\n\n  &:disabled {\n    background: $gainsBoro;\n  }\n\n  &.secondary {\n    color: $darkBlue;\n\n    .icon {\n      path {\n        fill: $midNightBlue;\n      }\n    }\n\n    &:hover {\n      background: $lightBlue;\n    }\n\n    &:disabled {\n      background: $powderBlue;\n    }\n  }\n}\n\n.success {\n  color: #fff;\n  border-color: $seaGreen;\n  background: $seaGreen;\n\n  --box-shadow-color: #{$seaGreen};\n\n  &:hover {\n    background: $darkGreen;\n  }\n\n  &:disabled {\n    background: $darkGreen;\n  }\n\n  &.secondary {\n    color: $seaGreen;\n\n    &:hover {\n      background: $lightGreen;\n    }\n\n    &:disabled {\n      background: $paleGreen;\n    }\n  }\n}\n\n.warning {\n  border-color: $orange;\n  background: $orange;\n\n  --box-shadow-color: #{$orange};\n\n  &:hover {\n    color: #fff;\n    background: $darkOrange;\n  }\n\n  &:disabled {\n    color: #fff;\n    background: $darkOrange;\n  }\n\n  &.secondary {\n    color: $darkOrange;\n\n    &:hover {\n      background: $lightOrange;\n    }\n\n    &:disabled {\n      background: $khaki;\n    }\n  }\n}\n\n.danger {\n  color: #fff;\n  border-color: $crimson;\n  background: $crimson;\n\n  --box-shadow-color: #{$crimson};\n\n  &:hover {\n    background: $darkRed;\n  }\n\n  &:disabled {\n    background: $darkRed;\n  }\n\n  &.secondary {\n    color: $crimson;\n\n    &:hover {\n      background: $lightRed;\n    }\n\n    &:disabled {\n      background: $coral;\n    }\n  }\n}\n"],"sourceRoot":""}]);
