+EU,8BFrFgB","sourcesContent":["// theme\n$white: #fff;\n$black: #000;\n$theme: #000;\n\n// navy blue\n$gainsBoro: rgb(101, 113, 135); // #657187\n$airForce: rgb(79, 91, 117); // #4f5b75\n$steel: rgb(61, 75, 102); // #3d4b66\n$space: rgb(35, 51, 81); // #233351\n$navy: rgb(10, 28, 59); // #0a1c3b\n\n// blue\n$lightBlue: rgb(243, 249, 255); // #f3f9ff\n$skyBlue: rgb(197, 226, 255); // #c5e2ff\n$deepSkyBlue: rgb(79, 163, 255); // #4fa3ff\n$dodgeBlue: rgb(12, 91, 255); // #0c5bff\n$darkBlue: rgb(9, 59, 154); // #093b9a\n\n// light blue\n$babyBlue: rgb(247, 250, 255); // #f7faff\n$powderBlue: rgb(216, 235, 255); // #d8ebff\n$royalBlue: rgb(120, 187, 255); // #78bbff\n$blue: rgb(1, 120, 254); // #0178fe\n$midNightBlue: rgb(0, 55, 132); // #003784\n\n// red\n$lightRed: rgb(255, 247, 247); // #fff7f7\n$coral: rgb(255, 186, 186); // #ffbaba\n$indianRed: rgb(255, 82, 82); // #ff5252\n$crimson: rgb(217, 34, 35); // #d92223\n$darkRed: rgb(155, 0, 0); // #9b0000\n\n// green\n$lightGreen: rgb(230, 252, 246); // #e6fcf6\n$paleGreen: rgb(135, 233, 200); // #87e9c8\n$springGreen: rgb(54, 205, 153); // #36cd99\n$seaGreen: rgb(12, 167, 111); // #0ca76f\n$darkGreen: rgb(0, 87, 58); // #00573a\n\n// orange\n$lightOrange: rgb(255, 252, 243); // #fffcf3\n$khaki: rgb(255, 234, 192); // #ffeac0\n$gold: rgb(255, 195, 90); // #ffc35a\n$orange: rgb(255, 163, 12); // #ffa30c\n$darkOrange: rgb(238, 104, 0); // #ee6800\n\n// gray\n$lightGray: rgb(246, 247, 248); // #f6f7f8\n$clouds: rgb(239, 240, 243); // #eff0f3\n$concrete: rgb(194, 199, 209); // #c2c7d1\n$asbestos: rgb(157, 164, 178); // #9da4b2\n$darkGray: rgb(126, 137, 156); // #7e899c\n\n/* stylelint-disable property-case */\n:export {\n  gainsBoro: $gainsBoro;\n  airForce: $airForce;\n  steel: $steel;\n  space: $space;\n  navy: $navy;\n  lightBlue: $lightBlue;\n  skyBlue: $skyBlue;\n  deepSkyBlue: $deepSkyBlue;\n  dodgeBlue: $dodgeBlue;\n  darkBlue: $darkBlue;\n  babyBlue: $babyBlue;\n  powderBlue: $powderBlue;\n  royalBlue: $royalBlue;\n 