rce;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n\n  @include transition(color);\n}\n\n.hide {\n  display: none;\n}\n\n.inputSearchOption {\n  color: $darkGray;\n}\n\n.inputWrapper {\n  margin-top: 5px;\n}\n\n.select {\n  width: 100%;\n  min-width: 240px;\n\n  .selectPrefix {\n    &:global {\n      &.__control {\n        min-height: 40px;\n        cursor: pointer;\n        border-radius: 8px;\n\n        @include transition();\n\n        &:hover {\n          border-color: $deepSkyBlue;\n        }\n\n        .__value-container {\n          padding-left: 10px;\n        }\n\n        &--menu-is-open {\n          box-shadow: 0 0 1px 2px $deepSkyBlue;\n\n          .__single-value {\n            color: $darkGray;\n          }\n        }\n      }\n\n      &.__placeholder {\n        color: $darkGray;\n      }\n\n      &.__menu {\n        z-index: 99;\n\n        @include animate(fadeIn);\n\n        &-list {\n          @media #{$phone} {\n            max-height: 220px;\n          }\n        }\n      }\n    }\n\n    :global(.__option--is-selected) {\n      .inputSearchOption {\n        color: darken($clouds, 2);\n      }\n    }\n\n    input {\n      width: auto !important;\n      opacity: 1 !important;\n      box-shadow: none;\n    }\n  }\n\n  &:global(.--is-disabled) {\n    .selectPrefix {\n      &:global {\n        &.__placeholder {\n          color: $concrete;\n        }\n      }\n    }\n\n    input {\n      color: $darkGray !important;\n      cursor: not-allowed;\n    }\n  }\n\n  &.error {\n    .selectPrefix {\n      &:global {\n        &.__control {\n          box-shadow: 0 0 1px 2px $crimson;\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
