: visible;\n  }\n\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n\n@keyframes fadeOutIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  90% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n","@import '@upp/chrome/styles/media';\n@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n\n.navigation {\n  position: relative;\n  display: none;\n}\n\n.wrap {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  min-height: 100vh;\n  background: #fff;\n  box-sizing: border-box;\n  z-index: 2;\n\n  & > div {\n    flex: 1 auto;\n  }\n\n  &.open {\n    position: relative;\n    margin-left: 0;\n  }\n}\n\n.menu {\n  top: 0;\n  display: flex !important;\n  flex-direction: column;\n  height: 100vh !important;\n  padding: 28px 20px 20px;\n  overflow: auto;\n  background: $white;\n  box-shadow: 0 0 4px -1px $darkGray;\n  box-sizing: border-box;\n  z-index: 9999 !important;\n  transition: all 0.5s ease !important;\n\n  :global(.bm-menu) {\n    display: flex;\n    flex: 1;\n    flex-direction: column;\n    height: auto !important;\n    overflow: initial !important;\n  }\n\n  :global(.bm-item-list) {\n    display: flex;\n    flex-direction: column;\n  }\n}\n\n.crossButton {\n  left: 8px !important;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  opacity: 0.5;\n\n  @include transition(opacity);\n\n  &:hover {\n    opacity: 1;\n  }\n\n  button {\n    border-radius: 3px;\n  }\n}\n\n.closeIcon {\n  width: 16px;\n  height: 16px;\n\n  path {\n    fill: $concrete;\n  }\n}\n"],"sourceRoot":""}]);
