$steel;\n  space: $space;\n  navy: $navy;\n  lightBlue: $lightBlue;\n  skyBlue: $skyBlue;\n  deepSkyBlue: $deepSkyBlue;\n  dodgeBlue: $dodgeBlue;\n  darkBlue: $darkBlue;\n  babyBlue: $babyBlue;\n  powderBlue: $powderBlue;\n  royalBlue: $royalBlue;\n  blue: $blue;\n  midNightBlue: $midNightBlue;\n  lightRed: $lightRed;\n  coral: $coral;\n  indianRed: $indianRed;\n  crimson: $crimson;\n  darkRed: $darkRed;\n  lightGreen: $lightGreen;\n  paleGreen: $paleGreen;\n  springGreen: $springGreen;\n  seaGreen: $seaGreen;\n  darkGreen: $darkGreen;\n  lightOrange: $lightOrange;\n  khaki: $khaki;\n  gold: $gold;\n  orange: $orange;\n  darkOrange: $darkOrange;\n  lightGray: $lightGray;\n  clouds: $clouds;\n  concrete: $concrete;\n  asbestos: $asbestos;\n  darkGray: $darkGray;\n  theme: $theme;\n  white: $white;\n  black: $black;\n}\n/* stylelint-enable property-case */\n","\n@mixin animate($animation, $duration: 0.3s, $delay: 0s) {\n  animation-name: $animation;\n  animation-duration: $duration;\n  animation-delay: $delay;\n  animation-fill-mode: both;\n}\n\n@mixin transition($rules: all, $delay: 0.3s, $type: ease) {\n  transition: $rules $delay $type;\n}\n\n@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n\n@keyframes fadeInLeft {\n  from {\n    opacity: 0;\n    visibility: hidden;\n    transform: translate(100px);\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n    transform: translate(0);\n  }\n}\n\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n    visibility: visible;\n  }\n\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n\n@keyframes fadeOutIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  90% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n","$mobile: 'screen and (max-width: 480px)';\n$phone: 'screen and (max-width: 767px)';\n$tablet: 'screen and (max-width: 991px)';\n$desktop: 'screen and (max-width: 1199px)';\n$large: 'screen and (max-width: 1599px)';\n$fullHD: 'screen and (max-width: 1919px)';\n\n/* stylelint-disable property-case */\n:export {\n  mobile: $mobile;\n  phone: $phone;\n  tablet: $tablet;\n  desktop: $desktop;\n  large: $large;\n  fullHD: $fullHD;\n}\n/* stylelint-enable property-case */\n","@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n@import '@upp/chrome/styles/mixins';\n@import '@upp/chrome/styles/media';\n\n.input {\n  height: 40px;\n  padding: 8px 12px;\n  padding-right: 30px;\n  border: 1px solid $concrete;\n  border-radius: 8px;\n  box-sizing: border-box;\n  -webkit-appearance: textfield;\n  -webkit-appearance: none;\n\n  @include transition();\n\n  &[type=search]::-webkit-search-decoration,\n  &[type=search]::-webkit-search-cancel-button,\n  &[type=search]::-webkit-search-results-button,\n  &[type=search]::-webkit-search-results-decoration {\n    display: none;\n  }\n\n  &[type=number] {\n    -moz-appearance: textfield;\n  }\n\n  &:focus {\n    border-color: $deepSkyBlue;\n  }\n\n  &:read-only,\n  &:disabled {\n    color: $asbestos;\n    cursor: not-allowed;\n    border-color: $clouds;\n    background: $clouds;\n    box-shadow: none;\n\n    &:hover {\n      border-color: $clouds !important;\n    }\n  }\n\n  &::placeholder {\n    color: $concrete;\n  }\n\n  &.error {\n    border-color: $crimson;\n\n    --box-shadow-color: #{$crimson};\n  }\n}\n\n.container {\n  position: relative;\n  width: 100%;\n  min-width: 200px;\n  max-width: 490px;\n\n  @media #{$phone} {\n    min-width: 100%;\n  }\n\n  &:hover {\n    .input {\n      border-color: $deepSkyBlue;\n    }\n  }\n}\n\n.inputWrapper {\n  position: relative;\n  margin-top: 5px;\n\n  .inputWithIcon {\n    padding-right: 55px;\n  }\n\n  .icon {\n    position: absolute;\n    top: 50%;\n    right: 12px;\n    width: 16px;\n    height: 16px;\n    transform: translateY(-50%);\n\n    path {\n      fill: $asbestos;\n    }\n  }\n\n  .iconCross {\n    width: 12px;\n    height: 12px;\n    opacity: 0;\n    cursor: pointer;\n\n    path {\n      fill: $asbestos;\n\n      @include transition(fill);\n    }\n\n    &:hover {\n      path {\n        fill: $concrete;\n      }\n    }\n\n    &.iconCrossRight {\n      right: 35px;\n    }\n  }\n}\n\n.show {\n  @include animate(fadeIn);\n}\n\n.hidden {\n  @include animate(fadeOut);\n}\n","/* stylelint-disable no-descending-specificity */\n@import '@upp/chrome/styles/media';\n@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n@import '@upp/chrome/styles/mixins';\n@import '../DefaultInput/DefaultInput.module.scss';\n\n.phone {\n  display: block;\n  width: 100%;\n  min-width: 240px;\n\n  .container {\n    margin-top: 5px;\n\n    .input {\n      @extend .input;\n\n      width: 100%;\n      padding-left: 52px;\n      transition-property: border, box-shadow;\n\n      &.error {\n        @extend .error;\n      }\n\n      &:global(.open) {\n        z-index: 0;\n      }\n    }\n\n    .button {\n      top: 1px;\n      bottom: 1px;\n      left: 1px;\n      border: 0;\n      border-radius: 8px;\n      background: $white;\n\n      :global(.selected-flag) {\n        position: absolute;\n        top: 0;\n        width: 48px;\n        padding-left: 16px;\n        box-sizing: initial;\n\n        &:focus {\n          box-shadow: none;\n        }\n\n        &:hover {\n          background: none;\n        }\n\n        :global(.flag) {\n          width: 24px;\n        }\n      }\n\n      :global(.country) {\n        display: flex;\n        align-items: center;\n        padding: 6px 8px;\n        color: $gainsBoro;\n        cursor: pointer;\n        border-radius: 8px;\n\n        @include transition();\n\n        &.checked,\n        &:hover {\n          color: $navy;\n          background: $lightGray;\n\n          .icon :global(path) {\n            fill: $navy;\n          }\n        }\n\n        &.checked {\n          cursor: default;\n        }\n\n        :global(.country-name) {\n          margin-left: 8px;\n        }\n\n        :global(.flag) {\n          position: relative;\n          top: 0;\n          left: 0;\n          width: 24px;\n          height: 24px;\n        }\n      }\n\n      &:global(.open) {\n        border-radius: 8px;\n\n        :global(.selected-flag) {\n          border-radius: 8px;\n          background: none;\n        }\n      }\n\n      &.disabledButton {\n        background: $lightGray;\n\n        &:focus,\n        :global(.selected-flag) {\n          box-shadow: none;\n        }\n      }\n    }\n\n    .select {\n      position: absolute;\n      top: calc(100% + 1px);\n      margin-top: 8px;\n      width: 320px;\n      min-width: 120px;\n      max-height: 200px;\n      padding: 8px 8px;\n      opacity: 0;\n      visibility: hidden;\n      border: 1px solid $clouds;\n      border-radius: 5px;\n      border-top: 0;\n      background-color: #fff;\n      box-shadow: 0 4px 6px -2px $concrete;\n      box-sizing: border-box;\n      z-index: 9;\n\n      @include animate(fadeIn);\n    }\n  }\n}\n"],"sourceRoot":""}]);
