lor: $asbestos;\n    border-color: $clouds;\n    background: $clouds;\n  }\n\n  &::placeholder {\n    color: $concrete;\n  }\n}\n\n.counter {\n  position: absolute;\n  top: calc(100% + 4px);\n  right: 0;\n  color: $asbestos;\n}\n\n.container {\n  position: relative;\n  display: inline-block;\n  width: 100%;\n  max-width: 708px;\n\n  &:hover {\n    .textarea {\n      border-color: $deepSkyBlue;\n    }\n  }\n\n  &.error {\n    .textarea {\n      border-color: $crimson;\n    }\n  }\n}\n"],"sourceRoot":""}]);
