ules: all, $delay: 0.3s, $type: ease) {\n  transition: $rules $delay $type;\n}\n\n@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n\n@keyframes fadeInLeft {\n  from {\n    opacity: 0;\n    visibility: hidden;\n    transform: translate(100px);\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n    transform: translate(0);\n  }\n}\n\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n    visibility: visible;\n  }\n\n  to {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n\n@keyframes fadeOutIn {\n  from {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  90% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  to {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n","@import '@upp/chrome/styles/colors';\n@import '@upp/chrome/styles/animate';\n@import '@upp/chrome/styles/mixins';\n\n.icon {\n  width: 32px;\n  height: 32px;\n\n  @include transition();\n\n  path {\n    fill: $darkBlue;\n  }\n}\n\n.button {\n  position: fixed;\n  top: 5%;\n  right: -300px;\n  display: flex;\n  padding: 16px 308px 16px 16px;\n  opacity: 0.75;\n  cursor: pointer;\n  border: 1px solid $concrete;\n  border-top-left-radius: 20px;\n  border-bottom-left-radius: 20px;\n  background: $white;\n  z-index: 9998;\n\n  @include transition(all, 0.5s);\n  @include animate(fadeInLeft, 1s);\n\n  &:hover {\n    opacity: 1;\n\n    .icon {\n      transform: rotate(360deg);\n    }\n  }\n\n  &.candidate {\n    border-color: $seaGreen;\n    background: $lightGreen;\n\n    .icon path {\n      fill: $seaGreen;\n    }\n  }\n}\n\n.opened {\n  margin-right: 300px;\n  opacity: 1;\n\n  .icon {\n    transform: rotate(360deg);\n  }\n\n  &:hover {\n    opacity: 1;\n    border-color: $concrete;\n\n    .icon {\n      transform: rotate(90deg);\n    }\n  }\n}\n"],"sourceRoot":""}]);
